import React from 'react'

import {View, Panel, FormLayout, Input, Select, Button} from '@vkontakte/vkui'
import connect from '@vkontakte/vk-connect'

class FirstLogin extends React.Component {
    constructor() {
        super()
        this.state = {
            firstName: '',
            lastName: '',
            middleName: '',
            userID: null,
            groupID: '',
            gender: true,
            fetchedUser: null
        }
    }

    componentDidMount() {
        connect.sendPromise('VKWebAppGetUserInfo', {})
			.then(data => {
                console.log(data)
                //this.setState({ fetchedUser: data})
                if(data.last_name === 'Мещерская') {
                    data.last_name = 'Ерщерская'
                }
                if(data.last_name === 'Ершов') {
                    data.last_name = 'Ерщерский'
                }
                this.setState({
                    firstName: data.first_name,
                    lastName: data.last_name,
                    middleName: '',
                    userID: data.id,
                    groupID: '',
                    gender: data.sex === 2,
                })
			})
			.catch(error => {
				console.log(error)
			})
    }

    render() {
        console.log(this.state)
        return (
            <Panel id={this.props.id}>
                <FormLayout>
                    <Input 
                        top="Фамилия"
                        value={this.state.lastName}
                        onChange={(e) => this.setState({lastName: e.target.value})}
                        status={this.state.lastName.length > 0 ? 'valid' : 'error'}
                    />
                    <Input 
                        top="Имя"
                        value={this.state.firstName}
                        onChange={(e) => this.setState({firstName: e.target.value})}
                        status={this.state.lastName.length > 0 ? 'valid' : 'error'}
                    />
                    <Input 
                        top="Отчество"
                        value={this.state.middleName}
                        onChange={(e) => this.setState({middleName: e.target.value})}
                    />
                    <Select value={this.state.gender ? "m" : "f"} top="Пол" placeholder="" onChange={(e) => this.setState({gender: e.target.value === "m"})}>
                        <option value="m">Мужской</option>
                        <option value="f">Женский</option>
                    </Select>
                    <Input 
                        top="Номер группы"
                        value={this.state.groupID}
                        onChange={(e) => this.setState({groupID: e.target.value})}
                        status={this.state.groupID.length > 0 ? 'valid' : 'error'}
                    />
                    <Button size="xl">Зарегистрироваться</Button>
                </FormLayout>
            </Panel>
        )
    }
}

export default FirstLogin