import React from 'react';
import connect from '@vkontakte/vk-connect';
import { View, Panel, ScreenSpinner } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

import FirstLogin from './panels/FirstLogin'

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'firstlogin',
			fetchedUser: null,
			popout: null,
		};
	}

	componentDidMount() {
		connect.sendPromise('VKWebAppGetUserInfo', {})
			.then(data => {
				this.setState({ fetchedUser: data})
			})
			.catch(error => {
				console.log(error)
			})
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<View activePanel={this.state.activePanel} popout={this.state.popout}>
				<Panel id='black'>

				</Panel>
				<FirstLogin id='firstlogin' />
			</View>
		);
	}
}

export default App;
